@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;600&display=swap');

html, body {
    
    font-family: 'Nunito';
    margin: 0;
    padding: 0;
}

body {
    background: #212527;
    color: #c3d7de;
}

.container {
    display: grid;
    width: 100%;
    margin: 2em auto 0;
}

.card {
    background: #337590;
    display: grid;
    grid-template-columns: 1fr 5fr;
    align-items: center;
    border-radius: 1em;
    margin-bottom: 2em;
}

p, h3 {
    margin: 0;
}

h3 {
    font-family: 'Montserrat';
    margin-left: 1em;
    margin-top: .5em
}
span {
    width: 5em;
    height: 5em;
    background: #75A2B8;
    border-radius: 50%;
}

img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
}

.container {
    width: 90%;
}

.card {
    background: #2C3031;
    padding: 1.5em;
}

.content {
    margin-left: 2em;
}

p.date {
    text-transform: uppercase;
    font-size: 0.75em;
    letter-spacing: 0.1em;
    margin-bottom: 0.4em;
    color: #7A8587
}

p {
    /* color: ghostwhite; */
}

h3 {
    font-size: 1.8em;
    margin: 0 0 0.3em;
    letter-spacing: 0.05em;
    color: #B6EAFF
}

.hoving {
    cursor: pointer;
    background: #3d856d;
    transition: all 0.7s;
    scale: 2;
    transform: scale(1.05)
}

.nothoving {
    cursor: default;
    transition: all 0.7s;
    transform: scale(1.00)
}
